@import "_flex.scss";
@import "_grid.scss";

/* border box by Paul Irish http://www.paulirish.com/2012/box-sizing-border-box-ftw/ */
html {
	box-sizing: border-box;
  	margin: 0;
	padding: 0;
}
*, *:before, *:after {
	box-sizing: inherit;
}

body {
	font-family: Arial, Helvetica, sans-serif; 
	min-height: 100vh;
	margin: 0;
}
header {
	overflow: auto;
	background-color: lightgrey;
	padding-left: 5em;
	padding-right: 5em;
}
h1 {
	margin-bottom: .1em;
}
ul{
		list-style-type: none;
		overflow: auto;
	}
	li {
		float: left;
	}
	a {
		text-decoration: none;
		color: blue;
		&:hover {
			text-decoration: underline;
		}
	}
nav {
	float: right;
	margin: 0 auto;
	li {
		@extend li;
		padding: 0.5em;
		margin-right:3em;
	}
	a {
		font-weight: 600;
		color: black;
		@extend a;
		&:hover {
			background-color: lightblue;
		}
	}
}
.container {
	width: 90%;
	margin: 0 auto;
}
// set styling for code snippets
code {
	font-size: larger;
	font-weight: 600;
}
// position info under title
// i {
// 	position: absolute;
// 	top: 4em;
// 	left: 5em;
// 	// float: right;
// 	// clear: right;
// }
footer {
	width: 100%;
	text-align: center;
}
main {
	border: 1px dashed lightgrey;
}
caption, th, td {
	border: 1px solid black;
	border-collapse: collapse;
	padding: .5em;
}
tr:not(:first-child) td:first-child {
	font-weight: bold;
}
table {
	@extend th;
	margin: 0 auto;
	margin-top: 1em;
}

aside {
	@extend main;
	li {
		margin-bottom: 1em;
	}
	h2 {
		text-align: center;
	}
}